export default class CommonUtils {
  /**
   * 从对象列表中获取指定key并构成一个新的对象数组
   * @param keys key数组
   * @param objectList 原始对象数组
   */
  static getObjectByKeys (keys, objectList) {
    if (objectList && keys) {
      let resultList = []
      objectList.forEach(object => {
        let result = {}
        keys.forEach(key => {
          result[key] = object[key]
        })
        resultList.push(result)
      })
      return resultList
    }
    return []
  }

  /**
   * 深拷贝对象
   * @param object 被拷贝对象
   */
  static clone (object) {
    if (object == null) {
      return null
    }
    let objectStr = JSON.stringify(object)
    return JSON.parse(objectStr)
  }
}
