<template>
  <el-form-item
      :label="column.label"
      :prop="column.key">
    <el-switch v-if="column.type === 'boolean'" v-model="model[column.key]"/>
    <el-select v-if="column.type === 'select'" v-model="model[column.key]" placeholder='请选择'>
      <el-option
          v-for="(dic, index) in (column.dicData ? column.dicData : [])"
          :label="dic.label"
          :value="dic.value"
          :key="index + 'option'"/>
    </el-select>
    <el-input-number v-if="column.type === 'number'" v-model="model[column.key]" :disabled="column.disabled"/>
    <el-radio-group v-if="column.type === 'radio'" v-model="model[column.key]">
      <el-radio
          v-for="(dic, index) in (column.dicData ? column.dicData : [])"
          :label="dic.label"
          :key="index + 'radio'">{{dic.value}}</el-radio>
    </el-radio-group>
    <el-input :placeholder="getPlaceholder(column)" v-if="column.type === 'password'" v-model="model[column.key]" show-password/>
    <el-input type='textarea' v-if="column.type === 'textarea'" v-model="model[column.key]" :placeholder="getPlaceholder(column)"/>
    <el-input :disabled="column.disabled" :placeholder="getPlaceholder(column)" v-if="column.type === 'input'" v-model="model[column.key]"/>
  </el-form-item>
</template>

<script>
export default {
  name: 'smart-form-item',
  props: {
    column: {
      required: true,
      type: Object
    },
    // 绑定model
    model: {
      required: true,
      type: Object
    }
  },
  methods: {
    getPlaceholder (column) {
      if (column.placeholder) {
        return column.placeholder
      } else {
        return '请输入' + column.label
      }
    }
  }
}
</script>

